.progress {
  width: 100%;
  background-color: #ddd;
}

.progress-bar {
  width: 1%;
  height: 20px;
  background-color: #008744;
}

.mining-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.machine-item {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  border-bottom: 2px solid gray;
  margin: 12px;
}

.mining-left  {
  -ms-flex: 1 1;
      flex: 1 1;
}

.mining-mid  {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.mining-right  {
  -ms-flex: 3 1;
      flex: 3 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.mining-tag {
  -ms-flex: 1 1;
      flex: 1 1;
  border: none;
}

.mine-button {
  width: auto;
  padding: 0;
  margin: 0;
  border: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-size: 16px;
  font-weight: lighter;
}

.mining-count {
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-pack: center;
      justify-content: center;
}

.mining-machine {
	width: 100%;
  margin-bottom: 10px;
  -webkit-transform: translatey(0px);
      -ms-transform: translatey(0px);
          transform: translatey(0px);
	-webkit-animation: float 2s ease-in-out infinite;
	        animation: float 2s ease-in-out infinite;
}

.mining-machine img{
  width: 90px;
}

.buy-button {
  width: 100px;
  padding: 10px;
  display: block;
  margin: 8px;
  background-color: #617db5;
  border: none;
  font-size: 18px;
}

.speed {
  position: relative;
}

.speed:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

.mine-price {
  position: relative;
}


.mine-price .tooltiptext {
  width: 300px;
}

.mine-price:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

@-webkit-keyframes float {
	0% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
	50% {
		-webkit-transform: translatey(-10px);
		        transform: translatey(-10px);
	}
	100% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}

@keyframes float {
	0% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
	50% {
		-webkit-transform: translatey(-10px);
		        transform: translatey(-10px);
	}
	100% {
		-webkit-transform: translatey(0px);
		        transform: translatey(0px);
	}
}




